@keyframes modalInAnimation {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@keyframes modalWindowInAnimation {
    0%{
        opacity: 0;
        transform: translateY(-100px);
    }
    100%{
        opacity: 1;
        transform: translateY(0px);
    }
}

.modal{
    opacity: 0;
    animation: modalInAnimation .2s ease forwards;
    transition: all ease .2s;
}

.modal .modal-window {
    opacity: 0;
    transform: translateY(-100px);
    animation: modalWindowInAnimation .4s ease forwards;
    transition: all ease .2s;
}