@keyframes showListItemAnimation {
    from{
        opacity: 0;
        transform: translate(100px);
    }
    to{
        opacity: 1;
        transform: translateX(0px);
    }
}

@keyframes showTrashAnimation {
    from{
        opacity: 0;
        transform: translateX(40px);
    }
    to{
        opacity: 1;
        transform: translateX(0px);
    }
}

@keyframes hideTrashAnimation {
    from{
        opacity: 1;
        transform: translateX(0px);
    }
    to{
        opacity: 0;
        transform: translateX(40px);
    }
}

.listItem{
    overflow: hidden;
    opacity: 0.2;
    animation: showListItemAnimation ease .4s forwards;
    transform: translateX(100px);
    transition: all ease .2s;

    .list-action-button{
        cursor: pointer;
        opacity: 0;
        transform: translateX(40px);
        animation: hideTrashAnimation ease .4s forwards;
        transition: all ease .2s;
    }
    &:hover{
        .list-action-button{
            animation: showTrashAnimation ease .4s forwards;
        }
    }
}