$info_color: #70bad2;
$warning_color: #ec926f;
$error_color: #f27474;
$success_color: #4caf50;
$question_color: #668ca9;
$waiting_color: rgb(71, 160, 255);

@keyframes boxIn {
  0% {
    opacity: 0;
    transform: scale(0.5);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes text-animation-stand {
  0% {
    opacity: 0.1;
  }
  100% {
    opacity: 1;
  }
}

.ActionInProcess {
  background: rgba(black, 0.2);
  color: black;
  position: absolute;
  top: 0;
  left: 0;
  cursor: progress;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
  flex-direction: row;
  z-index: 99999;
  opacity: 0;
  backdrop-filter: blur(0);
  transition: all ease .2s;

  &:active {
    cursor: not-allowed;
  }

  &,
  & * {
    user-select: none;
  }

  &.show {
    opacity: 1;
    display: flex;
    &.process {
      background: rgba(black, 0.6);
      backdrop-filter: blur(6px);
    }
  }

  &.hide {
    backdrop-filter: blur(0);
    opacity: 0;
    display: none;
  }

  

  .box {
    border-radius: 4px;
    box-shadow: rgba(black, 0.4) 0 2px 10px;
    background: white;
    color: black;
    padding: 20px;
    min-width: 400px;
    min-height: 260px;
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
    transition: all ease 0.2s;
    transform: scale(0.5);
    animation: boxIn ease 0.2s forwards;

    .section-top {
      display: flex;
      align-items: center;
      flex-direction: row;
      justify-content: center;
      width: 100%;

      .animation {
        width: 40%;
        height: 140px;
        text-align: center;
      }
    }

    .section-bottom {
      width: 100%;
      font-size: 1em;
      color: #333;
      padding: 4px 0;
      transition: all 0.2s ease;

      &.waiting {
        color: $waiting_color;
      }
      &.warning {
        color: $warning_color;
      }
      &.info {
        color: $info_color;
      }
      &.success {
        color: $success_color;
      }
      &.error {
        color: $error_color;
      }

      * {
        display: inline-block;
      }

      .title {
        display: block;
        font-size: 1.2em;
        color: #333;
      }
      .message {
        display: block;
        font-style: italic;
        text-transform: uppercase;

        &.waiting {
          animation: text-animation-stand 1.4s ease-in-out;
          animation-direction: alternate-reverse;
          animation-iteration-count: infinite;
        }
      }
    }
  }
}

.animation-waiting {
  position: relative;
  margin: 20px auto;
  width: 100px;
  height: 100px;
  background: rgb(255, 255, 255);
  border: solid 5px rgb(255, 255, 255);
  border-radius: 100%;
  overflow: hidden;
  animation: cssload-rotates 2.3s infinite linear;
  -o-animation: cssload-rotates 2.3s infinite linear;
  -ms-animation: cssload-rotates 2.3s infinite linear;
  -webkit-animation: cssload-rotates 2.3s infinite linear;
  -moz-animation: cssload-rotates 2.3s infinite linear;
}

.message{
  color: rgb(255, 255, 255);
  margin-left: 1rem;
  text-shadow: 0 2px 2px rgba(0, 0, 0, 0.8);
}

.circled-process-loader {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.circled-process-loader div {
  animation: circled-process-loader 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  transform-origin: 40px 40px;
}
.circled-process-loader div:after {
  content: " ";
  display: block;
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: purple !important;
  background-color: purple !important;
  opacity: 1;
  margin: -4px 0 0 -4px;
}
.circled-process-loader div:nth-child(1) {
  animation-delay: -0.036s;
}
.circled-process-loader div:nth-child(1):after {
  top: 63px;
  left: 63px;
}
.circled-process-loader div:nth-child(2) {
  animation-delay: -0.072s;
}
.circled-process-loader div:nth-child(2):after {
  top: 68px;
  left: 56px;
}
.circled-process-loader div:nth-child(3) {
  animation-delay: -0.108s;
}
.circled-process-loader div:nth-child(3):after {
  top: 71px;
  left: 48px;
}
.circled-process-loader div:nth-child(4) {
  animation-delay: -0.144s;
}
.circled-process-loader div:nth-child(4):after {
  top: 72px;
  left: 40px;
}
.circled-process-loader div:nth-child(5) {
  animation-delay: -0.18s;
}
.circled-process-loader div:nth-child(5):after {
  top: 71px;
  left: 32px;
}
.circled-process-loader div:nth-child(6) {
  animation-delay: -0.216s;
}
.circled-process-loader div:nth-child(6):after {
  top: 68px;
  left: 24px;
}
.circled-process-loader div:nth-child(7) {
  animation-delay: -0.252s;
}
.circled-process-loader div:nth-child(7):after {
  top: 63px;
  left: 17px;
}
.circled-process-loader div:nth-child(8) {
  animation-delay: -0.288s;
}
.circled-process-loader div:nth-child(8):after {
  top: 56px;
  left: 12px;
}
@keyframes circled-process-loader {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
