/* Global Rules */

.p-0 {
    padding: 0 !important;
}

.m-0 {
    margin: 0 !important;
}

.button-no-style {
    background: transparent;
    border: none;
    box-shadow: none;
    outline: none;
}

.transition-custom {
    transition: all ease .3s;
}

$fontsPath: "../assets/fonts/";

@font-face {
    font-family: "Open Sans";
    src: url($fontsPath + "Open_Sans/OpenSans-Regular.ttf");
}

@font-face {
    font-family: "Open Sans Light";
    src: url($fontsPath + "Open_Sans/OpenSans-Light.ttf");
}

@font-face {
    font-family: "Open Sans Bold";
    src: url($fontsPath + "Open_Sans/OpenSans-Bold.ttf");
}

@font-face {
    font-family: "Open Sans SemiBold";
    src: url($fontsPath + "Open_Sans/OpenSans-SemiBold.ttf");
}

.icon {
    padding: 0;
    margin: 0;
    display: inline-block;
    &.icon-sm {
        font-size: 16px;
    }
    &.icon-md {
        font-size: 20px;
    }
    &.icon-lg {
        font-size: 28px;
    }
}

/* Rules */

* {
    font-family: "Open Sans", sans-serif !important;
}

.App {
    overflow: hidden;
    width: 100vw;
    height: 100vh;
}

body {
    background: black;
    overflow-x: hidden;
    overflow-y: auto;
}

@keyframes viewin {
    0% {
        opacity: 0;
        transform: translateY(-20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.view {
    opacity: 0;
    transition: all ease 0.2s;
    transform: translateY(-20px);

    &.show {
        animation: viewin ease 0.4s forwards;
    }

    &.delay-02s {
        animation-delay: 0.2s;
    }
    &.delay-05s {
        animation-delay: 0.5s;
    }
    &.delay-07s {
        animation-delay: 0.7s;
    }
    &.delay-1s {
        animation-delay: 1s;
    }
    &.delay-15s {
        animation-delay: 1.5s;
    }
}


* {
    user-select: none;
}

.modal-xl {
    max-width: 95vw !important;
}


.image-profile{
    .photo{
        transition: all ease .2s;
    }
    .download-button{
        cursor: pointer;
        transition: all ease .2s;
        opacity: 0;
        &:hover{
            opacity: .9;
            transform: scale(1.1);
        }
        &:active{
            opacity: 1;
            transform: scale(1);
        }
    }

    &:hover{
        .photo{
            transform: scale(1.2);
        }
        .download-button{
            opacity: 1;
            
        }
    }
}

.burred{
    backdrop-filter: blur(10px);
}