@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

@keyframes blinkOpacityAnimation {
  from {
    opacity: 1;
  }
  to {
    opacity: .1;
  }
}

.blinkAnimation{
  animation: blinkOpacityAnimation ease infinite alternate-reverse;
}

.animation-duration-01{
  animation-duration: .1s;
}
.animation-duration-02{
  animation-duration: .2s;
}
.animation-duration-03{
  animation-duration: .3s;
}
.animation-duration-04{
  animation-duration: .4s;
}
.animation-duration-05{
  animation-duration: .5s;
}
.animation-duration-06{
  animation-duration: .6s;
}
.animation-duration-07{
  animation-duration: .7s;
}
.animation-duration-08{
  animation-duration: .8s;
}
.animation-duration-09{
  animation-duration: .9s;
}
.animation-duration-1{
  animation-duration: 1s;
}

.textSelectable{
  user-select: text;
}

html {
  scroll-behaviour: smooth;
}