.LoginFormScreen {
    width: 100vw;
    height: 100vh;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    -webkit-app-region: drag !important;

    .LoginFormScreenRow {

        .LoginFormScreenColumnLeft {
            background-color: rgba(3, 25, 134, 0.75);
        }

        .LoginFormScreenColumnRight {
            background-color: rgba(#17141e, 0.95);
            padding: 20px;
            overflow: auto;

            .text-info-login {
                color: #a9a9a9;
                font-size: 0.9rem;
            }
        }

        .login-buttons {
            
            button{
                transition: all ease 0.25s;
                &:hover {
                    transform: scale(1.05);
                    opacity: 0.8;
                }
                &:active {
                    transform: scale(0.95);
                    opacity: 1;
                }
            }

            
        }

        .login-right-footer {
            display: block;
            justify-content: center;
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: fit-content;
            margin: 0;

            .version-block {
                outline: none;
                border: none;
                position: relative;
                width: 70%;
                text-align: left;
                display: inline-block;
                font-size: 0.8rem;
            }

            .factoryResetButton {
                display: inline-block;
                flex: 1;
                color: white;
                border-radius: 4px;
                background-color: rgba(255, 201, 4, 0);
                outline: none;
                border: none;
                width: 30%;
                text-align: right;
                transition: all ease 0.25s;

                &:hover {
                    background-color: rgba(255, 201, 4, 1);
                    color: black;
                }

                * {
                    padding: 2px 0;
                    margin: 0 4px;
                }
            }
        }
    }
}
