
.clock-top{
		width: 29px;
		height: 40px;
		left:30px;
		overflow:hidden;
		background:rgb(204,238,255);
		position:absolute;
}
.clock-top:before,.clock-top:after{
		content:"";
		width:29px;height:29px;
		background:rgb(255,255,255);
		position:absolute;
		z-index:1;
}
.clock-top:before{
		top:29px;
		left:-17px;
		transform:rotate(45deg);
		-o-transform:rotate(45deg);
		-ms-transform:rotate(45deg);
		-webkit-transform:rotate(45deg);
		-moz-transform:rotate(45deg);
}
.clock-top:after{
		top:29px; left:17px;
		transform:rotate(45deg);
		-o-transform:rotate(45deg);
		-ms-transform:rotate(45deg);
		-webkit-transform:rotate(45deg);
		-moz-transform:rotate(45deg);
}
.a{
		position:relative;
		top:15px;
		width:29px;height:29px;
		background:rgba(0,123,255,0.97);
		animation: cssload-mymove 2.3s infinite linear;
		-o-animation: cssload-mymove 2.3s infinite linear;
		-ms-animation: cssload-mymove 2.3s infinite linear;
		-webkit-animation: cssload-mymove 2.3s infinite linear;
		-moz-animation: cssload-mymove 2.3s infinite linear;
}
.clock-bottom{
		width: 29px;
		height: 40px;
		left:30px;
		top:49px;
		overflow:hidden;
		background:rgb(204,238,255);
		position:absolute;
		z-index:1;
}
.clock-bottom:before,.clock-bottom:after{
		content:"";
		width:29px;
		height:39px;
		background:rgb(255,255,255);
		position:absolute;
		z-index:1;
}
.clock-bottom:before{
		top:-24px;
		left:-17px;
		transform:rotate(45deg);
		-o-transform:rotate(45deg);
		-ms-transform:rotate(45deg);
		-webkit-transform:rotate(45deg);
		-moz-transform:rotate(45deg);
}
.clock-bottom:after{
		top:-24px;
		left:23px;
		transform:rotate(45deg);
		-o-transform:rotate(45deg);
		-ms-transform:rotate(45deg);
		-webkit-transform:rotate(45deg);
		-moz-transform:rotate(45deg);
}
.b{
		position:relative;
		top:39px;
		width:29px;height:29px; background:rgb(71,160,255);
		animation: cssload-mymoveb 2.3s infinite linear;
		-o-animation: cssload-mymoveb 2.3s infinite linear;
		-ms-animation: cssload-mymoveb 2.3s infinite linear;
		-webkit-animation: cssload-mymoveb 2.3s infinite linear;
		-moz-animation: cssload-mymoveb 2.3s infinite linear;
}






@keyframes cssload-mymove{
		0%{top:15px;}
		100%{top:44px;}
}

@-o-keyframes cssload-mymove{
		0%{top:15px;}
		100%{top:44px;}
}

@-ms-keyframes cssload-mymove{
		0%{top:15px;}
		100%{top:44px;}
}

@-webkit-keyframes cssload-mymove{
		0%{top:15px;}
		100%{top:44px;}
}

@-moz-keyframes cssload-mymove{
		0%{top:15px;}
		100%{top:44px;}
}

@keyframes cssload-mymoveb{
		0%{top:39px;}
		90%{top:19px;}
		100%{top:-5px;}
}

@-o-keyframes cssload-mymoveb{
		0%{top:39px;}
		90%{top:19px;}
		100%{top:-5px;}
}

@-ms-keyframes cssload-mymoveb{
		0%{top:39px;}
