$info_color: #70bad2;
$warning_color: #ec926f;
$error_color: #f27474;
$success_color: #4CAF50;
$question_color: #668ca9;

@keyframes open-icon {
  0%{
    opacity: 0;
    transform: rotate3d(1, 1, 1, 140deg);
  }
  100%{
    opacity: 1;
    transform: rotate3d(1, 1, 1, 0deg);
  }
}

.animated-icon {
    position: relative;
    justify-content: center;
    width: 5em;
    height: 5em;
    margin: 1.25em auto 1.875em;
    border: .25em solid transparent;
    border-radius: 50%;
    line-height: 5em;
    cursor: default;
    box-sizing: content-box;
    user-select: none;
    transform: rotate3d(1, 1, 1, 140deg);
    opacity: 0;
    animation: open-icon .4s .3s ease forwards;

    .animated-icon-text{
      font-size: 3.75em;
    }

    &.animated-info {
      border-color: $info_color;
      color: $info_color;
    }

    &.animated-warning {
      border-color: $warning_color;
      color: $warning_color;
    }

    &.animated-error {
      border-color: $error_color;
      color: $error_color;
    }

    &.animated-success {
      border-color: $success_color;
      color: $success_color;
    }

    &.animated-question {
      border-color: $question_color;
      color: $question_color;
    }

}
